@tailwind base;
@layer base {
  h1 {
    @apply text-2xl font-bold;
  }
  h2 {
    @apply text-xl font-bold;
  }
  h3 {
    @apply text-lg font-bold;
  }
  h4 {
    @apply font-bold;
  }
  h5 {
    @apply text-sm font-bold;
  }
  h6 {
    @apply text-xs font-bold;
  }
  p {
    @apply mb-2 min-h-[16px];
  }
  html {
    @apply bg-red-50;
  }
  .block-content a {
    @apply border border-black pt-1 pb-1 pl-2 pr-2 hover:bg-black  hover:text-white;
  }
}

@tailwind components;
@tailwind utilities;
